
import { Options, Vue } from 'vue-class-component';
import { Prop, Emit } from 'vue-property-decorator';
import { TLink } from '@/types';

@Options({
  name: 'NavMenu',
})
export default class NavMenu extends Vue {
  @Prop({
    default: [],
  })
  items: TLink[] = [];
  @Emit('close') close(): boolean {
    return false;
  }
}
