
import { Options, Vue } from 'vue-class-component';
import { Prop } from 'vue-property-decorator';
import NavMenu from '@/components/navMenu/NavMenu.vue';
import { TLink } from '@/types';

@Options({
  name: 'MenuButton',
  components: { NavMenu },
})
export default class MenuButton extends Vue {
  @Prop({
    required: true,
    type: Array,
  })
  items!: TLink[];
  isOpenMenu = false;
}
